import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { workData } from '../../../shared/constants/workData'
import CustomBreadcrumb from '../../../shared/components/CustomBreadcrumb'
import Hexagon from '../../../shared/components/Hexagon'
import { Col, Row } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowRight, faCircleCheck } from '@fortawesome/free-solid-svg-icons'
import { route } from '../../../shared/constants/AllRoutes'
import FooterPage from '../../../shared/components/Footer'

const DetailWorkPage = () => {
    const { id } = useParams()
    const navigate = useNavigate()

    const [data, setData] = useState()
    const [altData, setAltData] = useState()

    useEffect(() => {
        setData(workData?.find(item => item._id === id))
        setAltData(workData?.filter((value) => value._id !== id))
    }, [id])

    return (
        <>
            <div className='details-work-page'>
                <CustomBreadcrumb />
                <Hexagon customPosition='top' isDark hideOnMd animationDelay='.4s' scale={2.1} top='-190px' right='20%' />
                <div className="work_content">
                    <div className="content_title">{data?.pageTitle}</div>
                    <div className="content_dec">
                        <Row className='justify-content-between'>
                            <Col xl={4} lg={7} md={12} sm={12}>
                                <div className="dec_title"><span>Your</span> Approach <br /><span>and</span> Work Specifics</div>
                                <div className="dec_data">{data?.description}</div>
                                <button className='common_btn btn_primary' onClick={() => navigate(route.work)}>
                                    <div className="text">view work</div>
                                    <div className="icon"><FontAwesomeIcon icon={faArrowRight} /></div>
                                </button>
                            </Col>
                            <Col xl={6} lg={5} md={12} sm={12}>
                                <ul className="content_tag">
                                    {data?.tags?.map((tag, index) => {
                                        return <li key={index}><span>{tag}</span><div className="icon"><FontAwesomeIcon icon={faCircleCheck} /></div></li>
                                    })}
                                </ul>
                            </Col>
                        </Row>
                    </div>
                </div>
            </div>
            <FooterPage />
        </>
    )
}

export default DetailWorkPage