import { faAngleRight } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { Fragment } from 'react'
import { Breadcrumb } from 'react-bootstrap'
import { Link as RouterLink, useLocation, useParams } from 'react-router-dom'
import { route } from '../../constants/AllRoutes'

const CustomBreadcrumb = () => {
    const { id } = useParams()
    const location = useLocation()

    const pathNames = location.pathname.split('/').filter((x) => x)
    const items = pathNames.filter((value) => value !== id)
    const currentPath = pathNames[pathNames.length - 1].replace(/-/g, ' ')

    return (
        <Breadcrumb className='breadcrumb-main' style={{ '--bs-breadcrumb-divider': '>' }}>
            <Breadcrumb.Item linkAs={RouterLink} linkProps={{ to: route.mainPage }}>Home Page</Breadcrumb.Item>

            {items.map((name, index) => {
                const routeTo = `/${items.slice(0, index + 1).join('/')}`;

                return (
                    <Fragment key={name}>
                        {index < items.length && <Breadcrumb.Item active className='arrow'><FontAwesomeIcon icon={faAngleRight} /></Breadcrumb.Item>}
                        <Breadcrumb.Item linkAs={RouterLink} linkProps={{ to: routeTo }}>{name}</Breadcrumb.Item>
                    </Fragment>
                )
            })}
            <Breadcrumb.Item active className='arrow'><FontAwesomeIcon icon={faAngleRight} /></Breadcrumb.Item>
            <Breadcrumb.Item active>{currentPath}</Breadcrumb.Item>

        </Breadcrumb>
    )
}

export default CustomBreadcrumb